<template>
  <div class="Classification" >
    <div v-for="(item, index) of arr" :key="item.id" @click="goclassdetails(item)">
      <img :src="imgs[index]"/>
      <span>{{ item.name }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Classification",
  props: ["arr"],
  data() {
    return {
      imgs: [
        require("@/assets/img/10000.png"),
        require("@/assets/img/10001.png"),
        require("@/assets/img/10002.png"),
        require("@/assets/img/10003.png"),
        require("@/assets/img/10004.png"),
        require("@/assets/img/10005.png"),
        require("@/assets/img/10006.png"),
        require("@/assets/img/10007.png"),
        require("@/assets/img/10008.png"),
        require("@/assets/img/10009.png"),
      ],
    };
  },
  methods:{
    goclassdetails(value){
      localStorage.setItem('title',value.name)
      localStorage.setItem('param',JSON.stringify({"channel":value.id,"serial_status":"","charge_type":"","page":1,"limit":200}))
      this.$router.push({
					name:'classdetails'
			})
    }
  }
};
</script>

<style lang="scss" scoped>
.Classification {
  width: 350px;
  padding-top: 29px;
  margin: 0 12.5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  div {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .1) ; 
    width: 160px;
    height: 90px;
    display: flex;
    background-color: #dfa;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    margin-bottom: 32px;
    img {
      width: 40px;
      height: 40px;
      margin-top: 10px;
    }
    span {
      display: block;
      font-size: 12px;
      margin-top: 14px;
      color: #fff;
    }
  }
  div:nth-child(1){
    background-color: #fea238;
  }
  div:nth-child(2){
    background-color: #554e40;
  }
  div:nth-child(3){
    background-color: #9e959e;
  }
  div:nth-child(4){
    background-color: #0277b4;
  }
  div:nth-child(5){
    background-color: #b98d7c;
  }
  div:nth-child(6){
    background-color: #95adae;
  }
  div:nth-child(7){
    background-color: #474885;
  }
  div:nth-child(8){
    background-color: #29333c;
  }
  div:nth-child(9){
    background-color: #234969;
  }
  div:nth-child(10){
    background-color: #7885cd;
  }
}
</style>