<template>
    <Classification :arr="children"></Classification>
</template>
  
  <script>
import axios from 'axios';
import Classification from '@/components/Classification.vue'

export default {
  name: "Mycation",
  components: {Classification},
  data() {
    return {
      children:[]
    }
  },
  mounted(){
    axios.get('https://jyapi.jyacg.com/web/book_cate').then(res=>{
      if(res.status == 200){
        this.children = res.data.data[1].children
        this.children.push(res.data.data[2].children[1])
      }
    })
  },
  activated(){
    this.$bus.$emit("active", 1);
  }
 
};
</script>
